.xback-board{
    width:100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index:20;

    /* if you want to control the z-inex, blur won't work */
    /* background-color: rgba(0, 0, 0, 0.459); */
}

.xback-board::before{
    content: "";
    position: fixed;
    bottom:0;
    right:0;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.459);
    z-index:-1;
    opacity: 0.4;
    filter: blur(10px);
}

.xsvg-container svg{
    width: 20px;
}

.modal-inner{
    background-color: white;
    border-radius: 10px;
    margin: 40px auto;
    padding: 32px;
    width: calc(80% - 240px);
    transform:translateX(120px);
}


/* edit modal */
.modal-inner-edit{
    background-color: white;
    border-radius: 10px;
    margin: 100px auto;
    width: calc(60% - 240px);
    transform:translateX(120px);
    color: var(--dark-blue-rep);
    /* padding: 0px 32px 32px; */
}
.modal-inner-edit-x{
    background-color: white;
    border-radius: 10px;
    margin: 100px auto;
    width: calc(70% - 240px);
    transform:translateX(60px);
    color: var(--dark-blue-rep);
}
.modal-edit-head{
    background-color: var(--dark-blue-rep);
    padding: 32px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}
.modal-headr{
    font-size: 24px;
    color: white;
}
.edit-modal-headr-x{
    font-size: 16px;
    color: white;
}
.flt-id{
    font-size: 12px;
    padding: 2.5px 14px;
    background-color: rgba(255, 255, 255, 0.1);
    position: relative;
    bottom: 20px;
}

/* multiselect style */
.chip{
    color: #152C5B;
    background-color:#ACB8CF;
    border-radius: 5px;
    border-radius: 2px;
}

.option:hover{
    background-color: #138fff!important;
}
.highlightOption{
    background-color: #138fff;
}
.highlight{
    background-color: #138fff;
}
.search-wrapper.searchWrapper{
    padding: 0px 5px;
}